* {
  box-sizing: border-box;
  margin: 0;
}

body {
  height: 100vh;
}

#container {
  height: 100%;
  width: 100%;
  background-color: #f3f3f3;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  font-family: Verdana, Tahoma, sans-serif;
  display: flex;
}

header, footer {
  height: 75px;
  width: 100%;
}

footer {
  justify-content: center;
  align-items: center;
  display: flex;
}

.github-link {
  color: #000;
  justify-content: center;
  align-items: center;
  gap: 30px;
  font-size: 30px;
  text-decoration: none;
  display: flex;
}

.github-logo {
  height: 45px;
}

.calc {
  height: 600px;
  width: 400px;
  flex-direction: column;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.calc-row {
  flex: 1;
  justify-content: space-between;
  gap: 7.5px;
  display: flex;
}

.output {
  width: 100%;
  color: #222;
  background-color: #e3e3e3;
  border: 2px solid #bbb;
  border-radius: 25px;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  padding: 0 15px;
  display: flex;
}

.operation {
  font-size: 18px;
}

.result {
  justify-content: flex-end;
  align-items: center;
  font-size: 35px;
  display: flex;
}

.btn {
  cursor: pointer;
  color: #222;
  background-color: #bbb;
  border: none;
  border-radius: 25px;
  flex: 1;
  justify-content: center;
  align-items: center;
  padding: 0;
  font-family: inherit;
  font-size: 30px;
  display: flex;
}

.btn:hover {
  background-color: #ccc;
}

.btn:active {
  background-color: #aaa;
}

.xl-btn {
  flex: 2.05;
}

.xxl-btn {
  flex: 3.1;
}

.btn:last-child {
  color: #fff;
  background-color: #f0964b;
}

.btn:last-child:hover {
  background-color: #ffa553;
}

.btn:last-child:active {
  background-color: #da8743;
}

@media (max-width: 457px) and (max-aspect-ratio: 8 / 15) {
  #container {
    justify-content: flex-start;
  }

  header, footer {
    height: 16.41vw;
  }

  .github-link {
    gap: 6.56vw;
    font-size: 6.56vw;
  }

  .github-logo {
    height: 9.84vw;
  }

  .calc {
    height: 131.25vw;
    width: 87.5vw;
    gap: 1.64vw;
  }

  .calc-row {
    gap: 1.64vw;
  }

  .output {
    border-radius: 5.47vw;
    padding: 0 3.28vw;
    font-size: 8.2vw;
  }

  .btn {
    border-radius: 5.47vw;
    font-size: 6.56vw;
  }
}

@media (max-height: 857px) and (min-aspect-ratio: 8 / 15) {
  #container {
    justify-content: flex-start;
  }

  header, footer {
    height: 8.75vh;
  }

  .github-link {
    gap: 3.5vh;
    font-size: 3.5vh;
  }

  .github-logo {
    height: 5.25vh;
  }

  .calc {
    height: 70vh;
    width: 46.67vh;
    gap: .875vh;
  }

  .calc-row {
    gap: .875vh;
  }

  .output {
    border-radius: 2.92vh;
    padding: 0 1.75vh;
    font-size: 4.375vh;
  }

  .btn {
    border-radius: 2.92vh;
    font-size: 3.5vh;
  }
}

